import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { TopBarComponent } from './top-bar/top-bar.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { RouterModule } from '@angular/router';
import { FacilityDetailsComponent } from './facility-details/facility-details.component';
import { FacilityPlanComponent } from './facility-plan/facility-plan.component';
import { LineChartComponent } from './charts/line-chart/line-chart.component';
import { IncidencesComponent } from './incidences/incidences.component';

@NgModule({
  declarations: [
    AppComponent,
    TopBarComponent,
    SidebarComponent,
    DashboardComponent,
    FacilityPlanComponent,
    LineChartComponent,
    FacilityDetailsComponent,
    IncidencesComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    RouterModule.forRoot([ 
      { path: '', component: DashboardComponent },
      { path: 'facility/:room', component: FacilityDetailsComponent },
      { path: 'incidences', component: IncidencesComponent}
    ])
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
