import {Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
//import { WebSocketService } from '../services/websocket.service';
import { LocalWebsocketService } from '../services/local-websocket.service';

@Component({
  selector: 'app-facility-details',
  templateUrl: './facility-details.component.html',
  styleUrls: ['./facility-details.component.css']
})

export class FacilityDetailsComponent implements OnInit {
  @Input()
  private dataType;

  private dataCollected: {
    temperature: number;
    luminosity: number;
    humidity: number;
  };

  private data;
  private room: string;

  constructor(
    private route: ActivatedRoute,
    //private dataService: WebSocketService
    private dataService: LocalWebsocketService
  ) { }

  ngOnInit() {
    this.route.paramMap.subscribe(params => {
        this.room = params.get('room');
      });
    this.initTopics();
  }

  private initTopics() {
    if (this.dataType === 'temperature') {
      this.dataService.temperature.subscribe(data => {
        this.debug(data);
        this.data = data;
        this.dataCollected.temperature = data.value;
      });
    }
    else if (this.dataType === 'luminosity') {
      this.dataService.luminosity.subscribe(data => {
        this.debug(data);
        this.data = data;
        this.dataCollected.luminosity = data.value;
      });
    }
    else if (this.dataType === 'humidity') {
      this.dataService.humidity.subscribe(data => {
        this.debug(data);
        this.data = data;
        this.dataCollected.humidity = data.value;
      });
    }
  }


  private debug(data) {
    console.log('Datos llegados: ' + data);
  }

}
